.App {
  text-align: center;
  background-color: #1f1515;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 20px;
}

.Counter_wrapper {
  width: 400px;
  height: 200px;
  margin: 10px;
  border: 3px solid aqua;
  padding: 20px;
  border-radius: 15px;
  background: #4d4242;
}

.Counter_btn_wrapper{
  display: flex;
  padding: 15px;
  justify-content: center;
  border: 3px solid aqua;
  border-radius: 10px;
  margin-top: 15px;
  background-color: lightslategray;
}

.Settings_wrapper {
  width: 400px;
  height: 200px;
  margin: 10px;
  border: 3px solid aqua;
  padding: 20px;
  border-radius: 15px;
  background: #4d4242;
}
